@import "./normalize"
@import "./vars"
@import "./mixins"
@import "./fonts"
@import "./sprite_generated"

// From libs:
//@import "../libs/LIB_NAME/path/to/style.scss"
@import "../libs/swiper/swiper-bundle.min.css"

html
   box-sizing: border-box

*,
*::before,
*::after
   box-sizing: border-box
    // min-height: 0.001vw //  Fixes Safari bug with viewport units in calc()

.content-box-component
   box-sizing: content-box

body
   width: 100%
   height: 100%
   min-height: 100vh
   display: flex
   flex-direction: column
   background: #fff
   color: #000
   font-family: 'Fira Sans', 'Helvetica', Arial, sans-serif
   font-weight: 300
   font-size: 12px
   line-height: 150%
   letter-spacing: 0.06em
   -webkit-font-smoothing: antialiased
   -moz-osx-font-smoothing: grayscale
   &.no-scroll
      overflow: hidden

.visually-hidden:not(:focus):not(:active)
   position: absolute
   width: 1px
   height: 1px
   margin: -1px
   border: 0
   padding: 0
   clip: rect(0 0 0 0)
   overflow: hidden

a
   text-decoration: none
   color: inherit

button
   padding: 0
   cursor: pointer
   background: transparent
   border: none
   color: inherit

ul, ol
   margin: 0
   padding: 0

ul
   list-style: none

p
   margin: 0

h1,
h2,
h3,
h4,
h5,
h6
   margin: 0

img
   display: block
   max-width: 100%
   height: auto

picture
   display: block

@media screen and (min-width: $width_tab)
   body
      font-size: 14px

@media screen and (min-width: $width_large)
   body
      font-size: 15px