:root {
  --padding: 14px;
  --margin: 40px;
  --secBg: #F5F5F5;
}

.address__time, .form .btn, .produce__text, .process__text-lead, .text__lead {
  color: #737373;
}

.footer__nav-list a, .header__nav-list a {
  transition: all 0.2s ease;
  position: relative;
}
.footer__nav-list a::before, .header__nav-list a::before {
  content: "";
  position: absolute;
  height: 1px;
  background: #000;
  bottom: 0;
  right: 0;
  width: 0;
  transition: all 0.2s ease;
}
.footer__nav-list a:hover::before, .header__nav-list a:hover::before, .footer__nav-list a:focus::before, .header__nav-list a:focus::before {
  width: 100%;
  right: auto;
  left: 0;
}

@media screen and (min-width: 768px) {
  :root {
    --padding: 22px;
  }
}
@media screen and (min-width: 1024px) {
  :root {
    --padding: 46px;
    --margin: 50px;
  }
}
@media screen and (min-width: 1440px) {
  :root {
    --padding: 130px;
    --margin: 60px;
  }
}
@media screen and (min-width: 1920px) {
  :root {
    --margin: 100px;
  }
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.content-box-component {
  box-sizing: content-box;
}

body {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: #fff;
  color: #000;
  font-family: "Fira Sans", "Helvetica", Arial, sans-serif;
  font-weight: 300;
  font-size: 12px;
  line-height: 150%;
  letter-spacing: 0.06em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body.no-scroll {
  overflow: hidden;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  padding: 0;
  cursor: pointer;
  background: transparent;
  border: none;
  color: inherit;
}

ul, ol {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

picture {
  display: block;
}

@media screen and (min-width: 768px) {
  body {
    font-size: 14px;
  }
}
@media screen and (min-width: 1920px) {
  body {
    font-size: 15px;
  }
}
.background-image {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}
.background-image picture {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.background-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.wrapper {
  display: block;
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
}

.container {
  width: 100%;
  max-width: 1670px;
  margin: 0 auto;
  padding: 0 var(--padding);
}

.title {
  margin-bottom: 20px;
  padding-bottom: 20px;
  font-family: "Georgia";
  font-weight: 700;
  font-size: 18px;
  line-height: 130%;
  letter-spacing: 0.1em;
  position: relative;
}
.title::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 80px;
  height: 2px;
  background: #F0D8C0;
}
.title--sec {
  padding-bottom: 12px;
  font-size: 20px;
}
.title--sec::after {
  width: 50px;
}

.btn {
  padding: 11px 27px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #000;
  color: #000;
  text-transform: uppercase;
  transition: all 0.2s ease;
  font-weight: 500;
  font-size: 13px;
  letter-spacing: 0.1em;
}
.btn:hover, .btn:focus {
  background: #000;
  color: #fff;
}
.btn--secondary {
  background: #000;
  color: #fff;
}
.btn--secondary:hover, .btn--secondary:focus {
  background: #fff;
  color: #000;
}
.btn--grey {
  color: #737373;
  border-color: #737373;
}

.btn-arrow {
  display: inline-flex;
  align-items: center;
  font-weight: 400;
  font-size: 13px;
  letter-spacing: 0.1em;
  color: #000000;
  text-transform: uppercase;
  transition: all 0.2s ease;
}
.btn-arrow span {
  margin-right: 24px;
  text-decoration: underline;
}
.btn-arrow img {
  transition: all 0.2s ease;
}
.btn-arrow:hover span, .btn-arrow:focus span {
  text-decoration: none;
}
.btn-arrow:hover img, .btn-arrow:focus img {
  transform: translateX(5px);
}

.logo {
  margin-right: auto;
  display: inline-flex;
  align-items: center;
  text-transform: uppercase;
  font-family: Georgia;
  font-weight: 700;
  line-height: 120%;
  font-size: 18px;
}
.logo h1 {
  font-size: 18px;
  line-height: 70%;
  letter-spacing: 0.12em;
}
.logo__icon {
  display: none;
  width: 31px;
  min-width: 31px;
  height: 31px;
  position: relative;
  top: -2px;
}
.logo span {
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 6px;
  line-height: 100%;
  letter-spacing: 0.1em;
  color: #A9A9A9;
}
.logo--sec {
  flex-direction: column;
  align-items: center;
  text-align: center;
  font-size: 21px;
}
.logo--sec h1 {
  font-size: 21px;
}
.logo--sec span {
  font-size: 7px;
}
.logo--sec .logo__icon {
  display: block;
  margin: 0;
  margin-bottom: 16px;
  position: static;
}

.call {
  width: 44px;
  min-width: 44px;
  height: 44px;
  border-radius: 50%;
  background: #000;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 100;
  left: var(--padding);
  bottom: 10px;
}
.call svg {
  width: 24px;
  height: 24px;
}

.text p {
  margin-bottom: 18px;
}
.text p:last-child {
  margin-bottom: 0;
}
.desc {
  padding: 20px;
  background-image: url("../images/process/process-lines.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.list li {
  margin-bottom: 12px;
  padding-left: 20px;
  position: relative;
}
.list li::before {
  content: "";
  position: absolute;
  left: 0;
  top: 4px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #F0D8C0;
}

@media screen and (min-width: 768px) {
  .logo {
    font-size: 28px;
  }
  .logo h1 {
    font-size: 28px;
  }
  .logo__icon {
    display: block;
    width: 42px;
    min-width: 42px;
    height: 42px;
    margin-right: 20px;
  }
  .logo span {
    font-size: 9px;
  }

  .title {
    font-size: 24px;
    line-height: 120%;
  }
  .title::after {
    width: 80px;
  }
  .title--sec {
    margin-bottom: 30px;
    font-size: 32px;
  }

  .btn {
    padding: 13px 38px 12px;
  }

  .call {
    display: none;
  }

  .desc {
    padding: 40px;
    background-image: url("../images/process/process-lines-tab.png");
    font-size: 15px;
    line-height: 150%;
  }

  .text p {
    margin-bottom: 22px;
  }
  .text .text__lead {
    margin-bottom: 38px;
  }
}
@media screen and (min-width: 1024px) {
  .title--sec {
    margin-bottom: 40px;
  }

  .logo--sec .logo__icon {
    margin-bottom: 20px;
  }

  .desc {
    background-image: url("../images/process/process-lines-desk.png");
  }
}
@media screen and (min-width: 1440px) {
  .title--sec {
    margin-bottom: 30px;
  }

  .desc {
    background-image: url("../images/process/process-lines-1440.png");
  }
}
@media screen and (min-width: 1920px) {
  .title {
    padding-bottom: 20px;
    font-size: 28px;
    letter-spacing: 0.06em;
  }
  .title--sec {
    margin-bottom: 54px;
    font-size: 40px;
    letter-spacing: 0.1em;
  }
}
.header__wrapper {
  min-height: 56px;
}
.header__inner {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: #fff;
  z-index: 100;
}
.header__top {
  min-height: 56px;
  padding: 12px var(--padding);
  display: flex;
  align-items: center;
  border-bottom: 1px solid var(--secBg);
}
.header__logo {
  margin-top: 5px;
}
.header__burger {
  width: 24px;
  min-width: 24px;
  height: 24px;
  margin-right: 20px;
}
.header__burger img {
  width: 100%;
  height: auto;
}
.header__menu {
  position: fixed;
  z-index: 200;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  background: #fff;
  transform: translateX(-100%);
  transition: transform 0.2s ease;
}
.header__menu--active {
  transform: translateX(0);
}
.header__menu-top {
  padding: 18px var(--padding);
  border-bottom: 1px solid #F5F5F5;
  display: flex;
  align-items: center;
}
.header__menu-close {
  width: 24px;
  height: 24px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}
.header__menu-bottom {
  padding: 44px 42px;
}
.header__menu .btn {
  width: 100%;
  min-height: 48px;
  color: #737373;
  border: 1px solid #737373;
}
.header__nav {
  margin-bottom: 50px;
}
.header__nav-list {
  font-size: 13px;
  line-height: 100%;
  letter-spacing: 0.15em;
  text-transform: uppercase;
  font-weight: 400;
}
.header__nav-list li {
  margin-bottom: 28px;
}
.header .address__tel {
  font-size: 25px;
}
.header .address__time {
  font-size: 11px;
}
.header .address__email {
  font-size: 11px;
  margin-bottom: 16px;
}
.header .address__location {
  font-size: 11px;
}

@media screen and (min-width: 768px) {
  .header__wrapper {
    min-height: 80px;
  }
  .header__top {
    min-height: 80px;
  }
  .header__burger {
    margin-right: 30px;
  }
  .header__menu-top {
    padding: 28px var(--padding) 18px;
  }
}
@media screen and (min-width: 1024px) {
  .header__wrapper {
    min-height: 108px;
  }
  .header__top {
    min-height: 108px;
    border-bottom: 1px solid #D9D9D9;
  }
  .header__burger {
    width: 34px;
    min-width: 34px;
    height: 34px;
    margin-right: 40px;
  }
  .header__menu-top {
    padding: 42px var(--padding) 18px;
  }
  .header__menu-bottom {
    padding: 44px var(--padding);
  }
}
@media screen and (min-width: 1280px) {
  .header {
    border-bottom: 1px solid #D9D9D9;
  }
  .header__inner {
    width: 100%;
    max-width: 1670px;
    margin: 0 auto;
    padding: 40px var(--padding) 0;
    position: static;
  }
  .header__top {
    margin-bottom: 38px;
    padding: 0;
    min-height: auto;
    border-bottom: none;
  }
  .header__burger {
    display: none;
  }
  .header__menu {
    height: auto;
    display: block;
    position: static;
    overflow-y: visible;
    transform: none;
  }
  .header__menu-top {
    display: none;
  }
  .header__menu .address {
    display: none;
  }
  .header__menu-bottom {
    padding: 0;
  }
  .header__nav {
    margin: 0;
  }
  .header__nav-list {
    display: flex;
    font-size: 12px;
  }
  .header__nav-list li {
    margin: 0;
    margin-right: 30px;
  }
  .header__nav-list a {
    display: block;
    padding-bottom: 20px;
    position: relative;
  }
  .header__nav-list a::before {
    height: 2px;
    bottom: -1px;
  }
  .header__nav-link--active::after {
    content: "";
    position: absolute;
    bottom: -1px;
    left: -15px;
    right: -15px;
    height: 2px;
    background: #737373;
  }
}
@media screen and (min-width: 1440px) {
  .header__nav-list li {
    margin-right: 50px;
  }
  .header__nav-link--active::after {
    left: -25px;
    right: -25px;
  }
}
.main {
  flex-grow: 1;
  padding-top: 24px;
}
.main.main--sec {
  padding-top: 0;
}

@media screen and (min-width: 768px) {
  .main {
    padding-top: 30px;
  }
}
@media screen and (min-width: 1024px) {
  .main {
    padding-top: 40px;
  }
}
@media screen and (min-width: 1920px) {
  .main {
    padding-top: 60px;
  }
}
.furniture {
  margin-bottom: var(--margin);
}
.furniture__slider {
  position: relative;
  overflow: hidden;
}
.furniture__slider .swiper-pagination {
  left: 32px;
  bottom: 18px;
  cfont-weight: 300;
  font-size: 12px;
  line-height: 100%;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #fff;
  font-family: "Fira Sans";
  text-align: left;
}
.furniture__slider .swiper-button-prev, .furniture__slider .swiper-button-next {
  display: none;
  width: 34px;
  height: 100%;
  transform: none;
  top: 0;
  bottom: 0;
  margin: 0;
  pointer-events: auto;
  transition: all 0.2s ease;
}
.furniture__slider .swiper-button-prev::after, .furniture__slider .swiper-button-next::after {
  color: #fff;
  font-size: 20px;
}
.furniture__slider .swiper-button-prev:hover, .furniture__slider .swiper-button-prev:focus, .furniture__slider .swiper-button-next:hover, .furniture__slider .swiper-button-next:focus {
  background: rgba(0, 0, 0, 0.2);
}
.furniture__slider .swiper-button-prev {
  left: 0;
}
.furniture__slider .swiper-button-next {
  right: 0;
}
.furniture__slide {
  height: auto;
  padding: 32px;
  padding-bottom: 50px;
  min-height: 314px;
  position: relative;
  background: #2F1A1B;
  color: #fff;
}
.furniture__title {
  margin-bottom: 12px;
  padding-bottom: 20px;
  font-weight: 700;
  font-size: 18px;
  line-height: 130%;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  font-family: "Georgia";
  position: relative;
}
.furniture__title::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 80px;
  height: 2px;
  background: #F0D8C0;
}
.furniture__info {
  max-width: 580px;
  position: relative;
  z-index: 5;
}
.furniture__text {
  margin-bottom: 12px;
  font-weight: 400;
